@use 'base/mixins' as m;
@use 'base/variables' as v;

.nuestros-testimoniales{
    padding: 10rem 3rem;

    @include m.tablet { 
        padding: 10rem;
    }

    .testimonial_contenedor{
        display: flex;
        flex-direction: column;
        gap: 2rem;
        @include m.tablet { 
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 3rem;
        }
        @include m.desktop { 
            grid-template-columns: repeat(3, 1fr);
        }
    }

    .testimonial{
        background-color: v.$cafeClaro;
        padding: 2rem 4rem;
        max-width: 50rem;
        margin: 0 auto;
        border-radius: 2rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        p{
            margin-bottom: 0;
        }

        .autor{
            text-align: right;
            font-size: 2.4rem;
            color: v.$primario;
            font-family: v.$fuenteCursiva;
        }

        @include m.tablet { 
            &:last-of-type{
                grid-column: 1 / 3;
            }
        }
        
        @include m.desktop { 
            &:last-of-type{
                grid-column: unset;
            }
            
        }
    }
}

    .testimonial_puntuacion{
        display: flex;
        justify-content: flex-end;
        align-items: flex-start;
        gap: 1rem;
        div{
            padding-top: .7rem;
            .color{
                color: v.$primario;
            }
        }
        p{
            margin: 0;
        }
    }