@use 'base/variables' as v;
@use 'base/mixins' as m;

.iconos{
    margin-top: 5rem;
    .listado-iconos{
        @include m.tablet { 
            @include m.grid(3, 2em);
        }
        li {
            img{
                margin: 0 auto;
                width: 12rem;
                height: auto;
            }
            p {
                font-family: v.$fuenteCursiva;
                font-size: 3.2rem;
                color: v.$primario;
            }
        }
    }
}