@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenido-contacto{
    grid-template-rows: auto;
    @include m.tablet { 
        @include m.grid(2, v.$separacion);
        // background-image: url(../img/granos_cafe.jpg);
        // background-repeat: no-repeat;
        // background-position: right center;
        // background-size: 40%;
    }
}

.formulario{
    .campo{
        margin-bottom: 2rem;
        
        &:last-child{
            margin-bottom: 0;
        }

        label{
            text-transform: uppercase;
            display: block;
            font-size: 2.2rem;
            font-weight: 700;
        }

        input:not([type="submit"]),
        select {
            width: 100%;
            padding: 1rem;
            border: .5rem solid v.$primario;
        }

        input[type="submit"]{
            background-color: v.$primario;
            width: 100%;
            border: none;
            padding: 1rem;
            color: v.$blanco;
            font-size: 2.2rem;
            font-weight: 700;
            text-transform: uppercase;
            
            &:hover{
                cursor: pointer;
            }
        }
    }
}

.imagen_contacto{
    width: 100%;
    height: 40rem;
    position: relative;
    @include m.tablet { 
        height: 100%;
    }
    img{
        margin: 2rem auto 0;
        height: 80%;
        position: absolute;
        left: 0;
        right: 0;

        @include m.tablet {
            width: 100%;
            bottom: 0;
        }
    }
}