@use 'base/variables' as v;
@use 'base/mixins' as m;
.header{
    padding: 0 0;
    position: relative;
    z-index: 4;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 80rem;
    @include m.tablet { 
        min-height: 70rem;
    }

    &::before{
        content: '';
        background-color: v.$negro;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: .6;
    }
    .contenido-header{
        position: relative;

        h1{
            text-align: center;
            color: v.$blanco;
            text-transform: uppercase;
            margin-top: 5rem;

            @include m.tablet { 
                margin-top: 10rem;
            }
        }
    }
}
