@use 'base/variables' as v;
@use 'base/mixins' as m;

.mensajeWhats{
    width: 6rem;
    height: 6rem;
    padding: 1.5rem;
    position: fixed;
    bottom: 1.5rem;
    right: 1.5rem;
    background-color: v.$primario;    
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    @include m.tablet { 
        bottom: 3rem;
        right: 5rem;
        z-index: 9;
    }

    .icono{
        font-size: 3rem;
        color: #fff;
    }
}